.container {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.img {
    width: 300px;
    height: 300;
    margin-top: 50px;
}

.textoFrase {
    font-style: italic;
    color: #dd7b22;
    text-align: center;
}

.btn_div {
    transform: translate(-159%, -290%);
    

}

.button {
    background-color: transparent;
    padding: 5px 10px;
    color: #dd7b22;
    text-transform: uppercase;
    font-size: 10px;
    letter-spacing: 2px;
    font-weight: 900;
    cursor: pointer;
    border: none;
    transition: all .4s;
    transform: rotate(30deg);
}

.button:hover {
    color: black;

       
}

.button:focus {
    outline: none;
}

.button:active {
    color: red;
}